export default "import { BiStar } from \"react-icons/bi\";\nimport {\n  BUTTON_COLOR,\n  VuiButtonPrimary,\n  VuiButtonSecondary,\n  VuiButtonTertiary,\n  VuiFlexContainer,\n  VuiFlexItem,\n  VuiIcon,\n  VuiSpacer,\n  VuiToggle\n} from \"../../../lib\";\nimport { Subsection } from \"../../components/Subsection\";\nimport { useState } from \"react\";\n\nexport const Colors = () => {\n  const [isLoading, setIsLoading] = useState<boolean>(false);\n  const [isSelected, setIsSelected] = useState<boolean>(false);\n  const [isInert, setIsInert] = useState<boolean>(false);\n  const [isDisabled, setIsDisabled] = useState<boolean>(false);\n\n  const icon = (\n    <VuiIcon>\n      <BiStar />\n    </VuiIcon>\n  );\n\n  return (\n    <>\n      <VuiToggle label=\"Loading\" checked={isLoading} onChange={() => setIsLoading(!isLoading)} />\n\n      <VuiSpacer size=\"m\" />\n\n      <VuiToggle label=\"Selected\" checked={isSelected} onChange={() => setIsSelected(!isSelected)} />\n\n      <VuiSpacer size=\"m\" />\n\n      <VuiToggle label=\"Inert (not clickable, non-visual)\" checked={isInert} onChange={() => setIsInert(!isInert)} />\n\n      <VuiSpacer size=\"m\" />\n\n      <VuiToggle\n        label=\"Disabled (not clickable, visual)\"\n        checked={isDisabled}\n        onChange={() => setIsDisabled(!isDisabled)}\n      />\n\n      <VuiSpacer size=\"m\" />\n\n      <Subsection title=\"Primary button\">\n        <VuiFlexContainer>\n          {BUTTON_COLOR.map((color) => (\n            <VuiFlexItem grow={false} key={color}>\n              <VuiButtonPrimary\n                isSelected={isSelected}\n                isDisabled={isDisabled}\n                isInert={isInert}\n                icon={icon}\n                color={color}\n                onClick={() => console.log(\"clicked\")}\n                isLoading={isLoading}\n              >\n                {color}\n              </VuiButtonPrimary>\n            </VuiFlexItem>\n          ))}\n        </VuiFlexContainer>\n      </Subsection>\n\n      <Subsection title=\"Secondary button\">\n        <VuiFlexContainer>\n          {BUTTON_COLOR.map((color) => (\n            <VuiFlexItem grow={false} key={color}>\n              <VuiButtonSecondary\n                isSelected={isSelected}\n                isDisabled={isDisabled}\n                isInert={isInert}\n                icon={icon}\n                color={color}\n                onClick={() => console.log(\"clicked\")}\n                isLoading={isLoading}\n              >\n                {color}\n              </VuiButtonSecondary>\n            </VuiFlexItem>\n          ))}\n        </VuiFlexContainer>\n      </Subsection>\n\n      <Subsection title=\"Tertiary button\">\n        <VuiFlexContainer>\n          {BUTTON_COLOR.map((color) => (\n            <VuiFlexItem grow={false} key={color}>\n              <VuiButtonTertiary\n                isSelected={isSelected}\n                isDisabled={isDisabled}\n                isInert={isInert}\n                icon={icon}\n                color={color}\n                onClick={() => console.log(\"clicked\")}\n                isLoading={isLoading}\n              >\n                {color}\n              </VuiButtonTertiary>\n            </VuiFlexItem>\n          ))}\n        </VuiFlexContainer>\n      </Subsection>\n    </>\n  );\n};\n";