export default "import { truncateEnd, truncateStart } from \"../../../lib/utils\";\n\nexport const TruncateString = () => {\n  const str = \"0123456789\";\n\n  return (\n    <>\n      <p>Original: {str}</p>\n      <p>Truncate 5 from start: {truncateStart(str, 5)}</p>\n      <p>Truncate 5 from end: {truncateEnd(str, 5)}</p>\n    </>\n  );\n};\n";