export default "import { useState } from \"react\";\nimport { VuiAccordion, VuiText } from \"../../../lib\";\n\nexport const Accordion = () => {\n  const [isOpen, setIsOpen] = useState(false);\n\n  return (\n    <VuiAccordion header=\"Open me\" isOpen={isOpen} setIsOpen={setIsOpen}>\n      <VuiText>\n        <p>Peek-a-boo!</p>\n      </VuiText>\n    </VuiAccordion>\n  );\n};\n";