export default "import { useState } from \"react\";\nimport {\n  AnchorSide,\n  VuiButtonSecondary,\n  VuiFormGroup,\n  VuiIcon,\n  VuiOptionsList,\n  VuiPopover,\n  VuiSelect,\n  VuiSpacer\n} from \"../../../lib\";\nimport { BiCaretDown } from \"react-icons/bi\";\n\nconst options = [\n  { value: \"apples\", label: \"Apples\" },\n  { value: \"windows\", label: \"Windows\" },\n  { value: \"penguins\", label: \"Penguins\" }\n];\n\nexport const Popover = () => {\n  const [isOpen, setIsOpen] = useState(false);\n  const [selectedOption, setSelectedOption] = useState(\"apples\");\n  const [anchorSide, setAnchorSide] = useState<AnchorSide>(\"right\");\n\n  return (\n    <>\n      <VuiFormGroup labelFor=\"anchorSideSelect\" label=\"Anchor side\">\n        <VuiSelect\n          id=\"anchorSideSelect\"\n          options={[\n            { text: \"Left\", value: \"left\" },\n            { text: \"Right\", value: \"right\" }\n          ]}\n          value={anchorSide}\n          onChange={(event) => setAnchorSide(event.target.value as AnchorSide)}\n        />\n      </VuiFormGroup>\n\n      <VuiSpacer size=\"m\" />\n\n      <VuiPopover\n        anchorSide={anchorSide}\n        isOpen={isOpen}\n        setIsOpen={() => setIsOpen(!isOpen)}\n        header=\"Tribes\"\n        button={\n          <VuiButtonSecondary\n            color=\"neutral\"\n            size=\"s\"\n            icon={\n              <VuiIcon size=\"m\">\n                <BiCaretDown />\n              </VuiIcon>\n            }\n          >\n            Tribe: {selectedOption}\n          </VuiButtonSecondary>\n        }\n      >\n        <VuiOptionsList\n          isSelectable\n          isScrollable\n          onSelectOption={(value: string) => {\n            setIsOpen(false);\n            setSelectedOption(value);\n          }}\n          selected={selectedOption}\n          options={options}\n        />\n      </VuiPopover>\n    </>\n  );\n};\n";