export default "import { BUTTON_COLOR, VuiHorizontalRule, VuiSpacer, VuiText } from \"../../../lib\";\n\nexport const HorizontalRule = () => {\n  return (\n    <>\n      {BUTTON_COLOR.map((color) => (\n        <>\n          <VuiText>\n            <p>{color}</p>\n          </VuiText>\n          <VuiSpacer size=\"m\" />\n          <VuiHorizontalRule color={color} />\n          <VuiSpacer size=\"xl\" />\n        </>\n      ))}\n    </>\n  );\n};\n";