export default "import { useEffect, useRef, useState } from \"react\";\nimport { VuiButtonSecondary, VuiSearchSelect } from \"../../../lib\";\n\ntype Option = { value: string; label: string };\n\n// Simulate a few pages worth of responses.\nconst optionsResponses = [\n  [\n    { value: \"a1\", label: \"A1\" },\n    { value: \"b1\", label: \"B1\" },\n    { value: \"c1\", label: \"C1\" },\n    { value: \"d1\", label: \"D1\" },\n    { value: \"e1\", label: \"E1\" },\n    { value: \"f1\", label: \"F1\" },\n    { value: \"g1\", label: \"G1\" },\n    { value: \"h1\", label: \"H1\" },\n    { value: \"i1\", label: \"I1\" },\n    { value: \"j1\", label: \"J1\" },\n    { value: \"k1\", label: \"K1\" },\n    { value: \"l1\", label: \"L1\" },\n    { value: \"m1\", label: \"M1\" }\n  ],\n  [\n    { value: \"n1\", label: \"N1\" },\n    { value: \"o1\", label: \"O1\" },\n    { value: \"p1\", label: \"P1\" },\n    { value: \"q1\", label: \"Q1\" },\n    { value: \"r1\", label: \"R1\" },\n    { value: \"s1\", label: \"S1\" },\n    { value: \"t1\", label: \"T1\" },\n    { value: \"u1\", label: \"U1\" },\n    { value: \"v1\", label: \"V1\" },\n    { value: \"w1\", label: \"W1\" },\n    { value: \"x1\", label: \"X1\" },\n    { value: \"y1\", label: \"Y1\" },\n    { value: \"z1\", label: \"Z1\" }\n  ],\n  [\n    { value: \"a2\", label: \"A2\" },\n    { value: \"b2\", label: \"B2\" },\n    { value: \"c2\", label: \"C2\" },\n    { value: \"d2\", label: \"D2\" },\n    { value: \"e2\", label: \"E2\" },\n    { value: \"f2\", label: \"F2\" },\n    { value: \"g2\", label: \"G2\" },\n    { value: \"h2\", label: \"H2\" },\n    { value: \"i2\", label: \"I2\" },\n    { value: \"j2\", label: \"J2\" },\n    { value: \"k2\", label: \"K2\" },\n    { value: \"l2\", label: \"L2\" },\n    { value: \"m2\", label: \"M2\" }\n  ],\n  [\n    { value: \"n2\", label: \"N2\" },\n    { value: \"o2\", label: \"O2\" },\n    { value: \"p2\", label: \"P2\" },\n    { value: \"q2\", label: \"Q2\" },\n    { value: \"r2\", label: \"R2\" },\n    { value: \"s2\", label: \"S2\" },\n    { value: \"t2\", label: \"T2\" },\n    { value: \"u2\", label: \"U2\" },\n    { value: \"v2\", label: \"V2\" },\n    { value: \"w2\", label: \"W2\" },\n    { value: \"x2\", label: \"X2\" },\n    { value: \"y2\", label: \"Y2\" },\n    { value: \"z2\", label: \"Z2\" }\n  ]\n];\n\nexport const Async = () => {\n  const pageRef = useRef(0);\n\n  const [isOpen, setIsOpen] = useState(false);\n  const [searchValue, setSearchValue] = useState<string>(\"\");\n  const [selectedOptions, setSelectedOptions] = useState([\"a1\", \"b1\"]);\n\n  // List of options that grows as we retrieve more pages.\n  const [lazyLoadedOptionsList, setLazyLoadedOptionsList] = useState<Array<Option>>([]);\n  // List of options limited to those that match a search query.\n  const [searchedOptionsList, setSearchedOptionsList] = useState<Array<Option>>([]);\n  const [isSearching, setIsSearching] = useState(false);\n\n  const fetchPage = async (newPage: number) => {\n    setIsSearching(true);\n    await new Promise((resolve) => setTimeout(resolve, 1000));\n    setLazyLoadedOptionsList((prev) => [...prev, ...optionsResponses[newPage]]);\n    setIsSearching(false);\n  };\n\n  useEffect(() => {\n    fetchPage(pageRef.current);\n  }, []);\n\n  useEffect(() => {\n    const search = async () => {\n      setIsSearching(true);\n      await new Promise((resolve) => setTimeout(resolve, 1000));\n      const response = optionsResponses\n        .flat()\n        .filter((option) => option.label.toLowerCase().includes(searchValue.toLowerCase()));\n      setSearchedOptionsList(response);\n      setIsSearching(false);\n    };\n\n    if (searchValue) {\n      search();\n    } else {\n      setSearchedOptionsList([]);\n    }\n  }, [searchValue]);\n\n  const asyncSearch = {\n    isSearching,\n    onSearchChange: (searchValue: string) => {\n      setSearchValue(searchValue);\n    },\n    onLazyLoad: () => {\n      if (pageRef.current < optionsResponses.length - 1) {\n        pageRef.current++;\n        fetchPage(pageRef.current);\n      }\n    }\n  };\n\n  // If there's a search, show the searched list otherwise show the lazy-loaded list.\n  const options = searchValue ? searchedOptionsList : lazyLoadedOptionsList;\n\n  const title =\n    !lazyLoadedOptionsList.length && isSearching\n      ? \"Loading\"\n      : selectedOptions.length\n      ? `Selected: ${selectedOptions.length === 1 ? \"1 item\" : `${selectedOptions.length} items`}`\n      : \"Select all that apply\";\n\n  return (\n    <VuiSearchSelect\n      isOpen={isOpen}\n      setIsOpen={setIsOpen}\n      searchValue={searchValue}\n      setSearchValue={setSearchValue}\n      onSelect={(value: string[]) => {\n        setSelectedOptions(value);\n      }}\n      options={options}\n      selectedOptions={selectedOptions}\n      asyncSearch={asyncSearch}\n    >\n      <VuiButtonSecondary color=\"neutral\" size=\"m\">\n        {title}\n      </VuiButtonSecondary>\n    </VuiSearchSelect>\n  );\n};\n";