export default "import { BiBookOpen, BiHelpCircle, BiSolidRocket, BiSolidWrench } from \"react-icons/bi\";\nimport {\n  VuiButtonPrimary,\n  VuiButtonTertiary,\n  VuiFlexContainer,\n  VuiFlexItem,\n  VuiGrid,\n  VuiIcon,\n  VuiLink,\n  VuiPanel,\n  VuiSpacer,\n  VuiText,\n  VuiTitle\n} from \"../../../lib\";\nimport circles from \"./circles.png\";\nimport lines from \"./lines.png\";\n\nexport const Panel = () => {\n  return (\n    <>\n      <VuiGrid columns={2}>\n        <VuiPanel\n          color=\"transparent\"\n          align=\"bottom\"\n          actions={\n            <VuiFlexContainer wrap>\n              <VuiFlexItem>\n                <VuiButtonPrimary color=\"primary\" size=\"l\">\n                  Try our sample data set\n                </VuiButtonPrimary>\n              </VuiFlexItem>\n\n              <VuiFlexItem>\n                <VuiButtonTertiary color=\"primary\" size=\"l\">\n                  Query your own data\n                </VuiButtonTertiary>\n              </VuiFlexItem>\n            </VuiFlexContainer>\n          }\n        >\n          <VuiTitle size=\"l\">\n            <h2>Welcome to Vectara</h2>\n          </VuiTitle>\n\n          <VuiSpacer size=\"s\" />\n\n          <VuiText size=\"l\">\n            <p>\n              Retrieval-augmented generation made easy. Get started with our sample data set or query your own data.\n            </p>\n          </VuiText>\n        </VuiPanel>\n\n        <VuiPanel\n          icon={\n            <VuiIcon isEnclosed color=\"primary\" size=\"xl\">\n              <BiSolidWrench />\n            </VuiIcon>\n          }\n        >\n          <VuiTitle size=\"m\">\n            <h3>Learn how to…</h3>\n          </VuiTitle>\n\n          <VuiSpacer size=\"m\" />\n\n          <VuiGrid columns={2}>\n            <VuiText size=\"l\">\n              <p>\n                <VuiLink href=\"#\">Build a proof-of-concept</VuiLink>\n              </p>\n            </VuiText>\n\n            <VuiText size=\"l\">\n              <p>\n                <VuiLink href=\"#\">Securely deploy Vectara apps</VuiLink>\n              </p>\n            </VuiText>\n          </VuiGrid>\n\n          <VuiSpacer size=\"s\" />\n\n          <VuiGrid columns={2}>\n            <VuiText size=\"l\">\n              <p>\n                <VuiLink href=\"#\">Build a chatbot</VuiLink>\n              </p>\n            </VuiText>\n\n            <VuiText size=\"l\">\n              <p>\n                <VuiLink href=\"#\">Engineer prompts</VuiLink>\n              </p>\n            </VuiText>\n          </VuiGrid>\n\n          <VuiSpacer size=\"s\" />\n\n          <VuiGrid columns={2}>\n            <VuiText size=\"l\">\n              <p>\n                <VuiLink href=\"#\">Implement hybrid search</VuiLink>\n              </p>\n            </VuiText>\n\n            <VuiText size=\"l\">\n              <p>\n                <VuiLink href=\"#\">See all tutorials</VuiLink>\n              </p>\n            </VuiText>\n          </VuiGrid>\n        </VuiPanel>\n      </VuiGrid>\n\n      <VuiSpacer size=\"m\" />\n\n      <VuiGrid columns={3}>\n        <VuiPanel\n          background={lines}\n          backgroundScale=\"height\"\n          icon={\n            <VuiIcon isEnclosed color=\"primary\" size=\"xl\">\n              <BiBookOpen />\n            </VuiIcon>\n          }\n          actions={\n            <VuiFlexContainer>\n              <VuiFlexItem>\n                <VuiButtonTertiary noPadding color=\"primary\" size=\"l\">\n                  Docs\n                </VuiButtonTertiary>\n              </VuiFlexItem>\n\n              <VuiFlexItem>\n                <VuiButtonTertiary color=\"primary\" size=\"l\">\n                  API reference\n                </VuiButtonTertiary>\n              </VuiFlexItem>\n            </VuiFlexContainer>\n          }\n        >\n          <VuiTitle size=\"m\">\n            <h3>Read the docs</h3>\n          </VuiTitle>\n        </VuiPanel>\n\n        <VuiPanel\n          icon={\n            <VuiIcon isEnclosed color=\"primary\" size=\"xl\">\n              <BiHelpCircle />\n            </VuiIcon>\n          }\n        >\n          <VuiTitle size=\"m\">\n            <h3>Get help</h3>\n          </VuiTitle>\n\n          <VuiSpacer size=\"s\" />\n\n          <VuiText size=\"l\">\n            <p>\n              Email us at <a href=\"#\">feedback@vectara.com</a>, post on our <a href=\"#\">forums</a>, or visit our{\" \"}\n              <a href=\"#\">Discord</a>.\n            </p>\n          </VuiText>\n        </VuiPanel>\n\n        <VuiPanel\n          background={circles}\n          backgroundScale=\"width\"\n          icon={\n            <VuiIcon isEnclosed color=\"primary\" size=\"xl\">\n              <BiSolidRocket />\n            </VuiIcon>\n          }\n          actions={\n            <VuiButtonPrimary color=\"primary\" size=\"l\">\n              Schedule demo\n            </VuiButtonPrimary>\n          }\n        >\n          <VuiTitle size=\"m\">\n            <h3>Schedule a demo</h3>\n          </VuiTitle>\n\n          <VuiSpacer size=\"s\" />\n\n          <VuiText size=\"l\">\n            <p>Our team will show you how you can solve your business's GenAI problems.</p>\n          </VuiText>\n        </VuiPanel>\n      </VuiGrid>\n\n      <VuiSpacer size=\"m\" />\n\n      <VuiPanel\n        background={circles}\n        backgroundScale=\"height\"\n        backgroundMaxSize=\"300px\"\n        actions={\n          <VuiButtonPrimary color=\"primary\" size=\"l\">\n            Do something\n          </VuiButtonPrimary>\n        }\n      >\n        <VuiTitle size=\"m\">\n          <h3>Testing out background max-size</h3>\n        </VuiTitle>\n\n        <VuiSpacer size=\"s\" />\n\n        <VuiText size=\"l\">\n          <p>The background image scales with the height of the container but it's capped at a height of 300px.</p>\n        </VuiText>\n      </VuiPanel>\n    </>\n  );\n};\n";