export default "import { BiStar } from \"react-icons/bi\";\nimport { ICON_SIZE, VuiFlexContainer, VuiFlexItem, VuiIcon, VuiText } from \"../../../lib\";\n\nexport const Sizes = () => {\n  return (\n    <VuiFlexContainer>\n      {ICON_SIZE.map((size) => (\n        <VuiFlexItem grow={false} key={size}>\n          <VuiText>\n            <p>Size {size}</p>\n          </VuiText>\n\n          <VuiIcon size={size}>\n            <BiStar />\n          </VuiIcon>\n        </VuiFlexItem>\n      ))}\n    </VuiFlexContainer>\n  );\n};\n";