export default "import { useState } from \"react\";\nimport { VuiCheckbox, VuiSpacer } from \"../../../lib\";\n\nexport const WithLabel = () => {\n  const [pizzaToppings, setPizzaToppings] = useState({\n    pepperoni: true,\n    mushrooms: false,\n    jalapenos: false\n  });\n\n  return (\n    <>\n      <VuiCheckbox\n        label=\"Pepperoni\"\n        onChange={() =>\n          setPizzaToppings({\n            ...pizzaToppings,\n            pepperoni: !pizzaToppings.pepperoni\n          })\n        }\n        checked={pizzaToppings.pepperoni}\n      />\n\n      <VuiSpacer size=\"s\" />\n\n      <VuiCheckbox\n        label=\"Mushrooms\"\n        onChange={() =>\n          setPizzaToppings({\n            ...pizzaToppings,\n            mushrooms: !pizzaToppings.mushrooms\n          })\n        }\n        checked={pizzaToppings.mushrooms}\n      />\n\n      <VuiSpacer size=\"s\" />\n\n      <VuiCheckbox\n        label=\"Jalapeños\"\n        onChange={() =>\n          setPizzaToppings({\n            ...pizzaToppings,\n            jalapenos: !pizzaToppings.jalapenos\n          })\n        }\n        checked={pizzaToppings.jalapenos}\n      />\n    </>\n  );\n};\n";