export default "import { VuiInfoTable, VuiText, InfoTableRowType, InfoTableColumnAlign } from \"../../../lib\";\n\nconst renderCell = (value: string, isEmphasized?: boolean) => {\n  if (isEmphasized) {\n    return (\n      <>\n        <VuiText>\n          <p>\n            <strong>{value}</strong>\n          </p>\n        </VuiText>\n      </>\n    );\n  }\n  return (\n    <>\n      <VuiText>\n        <p>{value}</p>\n      </VuiText>\n    </>\n  );\n};\n\nexport const Complex = () => {\n  const columns = [\n    {\n      name: \"item\",\n      render: \"Item\"\n    },\n    {\n      name: \"quantity\",\n      render: \"Quantity\",\n      width: \"80px\",\n      align: \"right\" as InfoTableColumnAlign\n    },\n    {\n      name: \"unitPrice\",\n      render: \"Unit price\",\n      width: \"120px\",\n      align: \"right\" as InfoTableColumnAlign\n    },\n    {\n      name: \"cost\",\n      render: \"Cost\",\n      width: \"120px\",\n      align: \"right\" as InfoTableColumnAlign\n    }\n  ];\n\n  const rows = [\n    {\n      type: \"sectionHeader\" as InfoTableRowType,\n      values: {\n        item: {\n          render: renderCell(\"Standard\", true),\n          colSpan: 4\n        }\n      }\n    },\n    {\n      values: {\n        item: {\n          render: renderCell(\"Base plan\")\n        },\n        quantity: {\n          render: renderCell(\"1\")\n        },\n        unitPrice: {\n          render: renderCell(\"$0\")\n        },\n        cost: {\n          render: renderCell(\"$0\")\n        }\n      }\n    },\n    {\n      type: \"sectionHeader\" as InfoTableRowType,\n      values: {\n        item: {\n          render: renderCell(\"Miscellaneous\", true),\n          colSpan: 4\n        }\n      }\n    },\n    {\n      values: {\n        item: {\n          render: renderCell(\"Bundles\")\n        },\n        quantity: {\n          render: renderCell(\"2\")\n        },\n        unitPrice: {\n          render: renderCell(\"$1.25\")\n        },\n        cost: {\n          render: renderCell(\"$2.50\")\n        }\n      }\n    },\n    {\n      values: {\n        item: {\n          render: renderCell(\"DTS\")\n        },\n        quantity: {\n          render: renderCell(\"5\")\n        },\n        unitPrice: {\n          render: renderCell(\"$2.00\")\n        },\n        cost: {\n          render: renderCell(\"$10.00\")\n        }\n      }\n    },\n    {\n      values: {\n        item: {\n          render: renderCell(\"One-time fee\"),\n          colSpan: 3\n        },\n        cost: {\n          render: renderCell(\"$1.00\")\n        }\n      }\n    },\n    {\n      type: \"footer\" as InfoTableRowType,\n      values: {\n        item: {\n          render: renderCell(\"Total cost\", true),\n          colSpan: 3\n        },\n        cost: {\n          render: renderCell(\"$13.50\", true)\n        }\n      }\n    }\n  ];\n\n  return <VuiInfoTable columns={columns} rows={rows} padding=\"s\" isHeaderVisible />;\n};\n";