export default "import { VuiTitle, TITLE_SIZE, VuiSpacer } from \"../../../lib\";\n\nexport const Title = () => (\n  <>\n    {TITLE_SIZE.concat()\n      .reverse()\n      .map((size) => (\n        <>\n          <VuiTitle size={size} key={size}>\n            <h5>Title size {size}</h5>\n          </VuiTitle>\n          <VuiSpacer size=\"xxs\" />\n        </>\n      ))}\n  </>\n);\n";