export default "import { useState } from \"react\";\nimport { OptionListItem, VuiIcon, VuiOptionsList } from \"../../../lib\";\nimport { BiCopy, BiEdit, BiTrash } from \"react-icons/bi\";\n\nconst options: OptionListItem<string>[] = [\n  {\n    value: \"edit\",\n    label: \"Edit\",\n    icon: (\n      <VuiIcon>\n        <BiEdit />\n      </VuiIcon>\n    )\n  },\n  {\n    value: \"copy\",\n    label: \"Copy\",\n    icon: (\n      <VuiIcon>\n        <BiCopy />\n      </VuiIcon>\n    )\n  },\n  {\n    value: \"delete\",\n    label: \"Delete\",\n    color: \"danger\",\n    icon: (\n      <VuiIcon>\n        <BiTrash />\n      </VuiIcon>\n    )\n  }\n];\n\nexport const IconsAndColors = () => {\n  const [selectedOption, setSelectedOption] = useState(\"a\");\n\n  return (\n    <VuiOptionsList\n      onSelectOption={(value: string) => {\n        setSelectedOption(value);\n        alert(`Selected ${value}`);\n      }}\n      selected={selectedOption}\n      options={options}\n    />\n  );\n};\n";