export default "import { VuiBadge, VuiFlexContainer, VuiFlexItem } from \"../../../lib\";\n\nexport const ClickableBadges = () => {\n  return (\n    <VuiFlexContainer>\n      <VuiFlexItem grow={false}>\n        <VuiBadge color=\"primary\" href=\"https://docs.vectara.com/\" target=\"_blank\">\n          Docs\n        </VuiBadge>\n      </VuiFlexItem>\n\n      <VuiFlexItem grow={false}>\n        <VuiBadge color=\"primary\" onClick={() => alert(\"Hello, world\")}>\n          Hello, world\n        </VuiBadge>\n      </VuiFlexItem>\n    </VuiFlexContainer>\n  );\n};\n";