export default "import { VuiButtonPrimary } from \"../../../lib\";\n\nexport const Submit = () => {\n  return (\n    <form\n      onSubmit={(event) => {\n        event.preventDefault();\n        alert(\"Form submitted\");\n      }}\n    >\n      <VuiButtonPrimary color=\"primary\" isSubmit>\n        Submit form\n      </VuiButtonPrimary>\n    </form>\n  );\n};\n";