export default "import { useState } from \"react\";\nimport { BiAnalyse, BiListCheck, BiSolidFlask } from \"react-icons/bi\";\nimport {\n  VuiAccordion,\n  VuiButtonSecondary,\n  VuiCallout,\n  VuiIcon,\n  VuiInfoTable,\n  VuiSpacer,\n  VuiText,\n  VuiTimeline,\n  VuiTimelineItem,\n  VuiTitle\n} from \"../../../lib\";\n\nexport const Timeline = () => {\n  const [isOpen, setIsOpen] = useState(false);\n\n  const columns = [\n    {\n      name: \"ingredient\",\n      render: \"Ingredient\"\n    },\n    {\n      name: \"amount\",\n      render: \"Amount\",\n      width: \"200px\"\n    }\n  ];\n\n  const rows = [\n    {\n      values: {\n        ingredient: {\n          render: (\n            <VuiText>\n              <p>Plutonium</p>\n            </VuiText>\n          )\n        },\n        amount: {\n          render: (\n            <VuiText>\n              <p>4 grams</p>\n            </VuiText>\n          )\n        }\n      }\n    },\n    {\n      values: {\n        ingredient: {\n          render: (\n            <VuiText>\n              <p>TGRI secret sauce</p>\n            </VuiText>\n          )\n        },\n        amount: {\n          render: (\n            <VuiText>\n              <p>8.25 microliters</p>\n            </VuiText>\n          )\n        }\n      }\n    },\n    {\n      values: {\n        ingredient: {\n          render: (\n            <VuiText>\n              <p>Corn starch</p>\n            </VuiText>\n          )\n        },\n        amount: {\n          render: (\n            <VuiText>\n              <p>1 tbsp</p>\n            </VuiText>\n          )\n        }\n      }\n    }\n  ];\n\n  return (\n    <VuiTimeline>\n      <VuiTimelineItem\n        icon={\n          <VuiIcon color=\"subdued\">\n            <BiListCheck />\n          </VuiIcon>\n        }\n      >\n        <VuiTitle size=\"l\">\n          <h2>Ingredients for making ooze</h2>\n        </VuiTitle>\n        <VuiSpacer size=\"s\" />\n\n        <VuiInfoTable columns={columns} rows={rows} padding=\"s\" isHeaderVisible={true} />\n      </VuiTimelineItem>\n\n      <VuiTimelineItem\n        icon={\n          <VuiIcon color=\"subdued\">\n            <BiAnalyse />\n          </VuiIcon>\n        }\n      >\n        <VuiTitle size=\"m\">\n          <h2>How to prepare</h2>\n        </VuiTitle>\n\n        <VuiSpacer size=\"s\" />\n\n        <VuiAccordion header=\"Instructions\" isOpen={isOpen} setIsOpen={setIsOpen}>\n          <VuiText>\n            <ol>\n              <li>Secure mutagens</li>\n              <li>Put in beaker</li>\n              <li>Shake thoroughly</li>\n              <li>Do not drink!!</li>\n              <li>Baste teenage ninja turtles lightly and let sit 20 minutes</li>\n            </ol>\n          </VuiText>\n        </VuiAccordion>\n      </VuiTimelineItem>\n\n      <VuiTimelineItem\n        icon={\n          <VuiIcon color=\"subdued\">\n            <BiSolidFlask />\n          </VuiIcon>\n        }\n      >\n        <VuiCallout color=\"neutral\" title=\"Outcome\" headingElement=\"h2\">\n          <VuiText>\n            <p>Mutated teenage ninja turtles.</p>\n          </VuiText>\n\n          <VuiSpacer size=\"m\" />\n          <VuiButtonSecondary color=\"primary\">Deploy to sewers</VuiButtonSecondary>\n        </VuiCallout>\n      </VuiTimelineItem>\n    </VuiTimeline>\n  );\n};\n";