export default "import { BiStar } from \"react-icons/bi\";\nimport { ICON_COLOR, VuiFlexContainer, VuiFlexItem, VuiIcon, VuiText } from \"../../../lib\";\n\nexport const Colors = () => {\n  return (\n    <VuiFlexContainer>\n      {ICON_COLOR.map((color) => (\n        <VuiFlexItem grow={false} key={color}>\n          <VuiText>\n            <p>Color {color}</p>\n          </VuiText>\n\n          <VuiIcon color={color} size=\"m\">\n            <BiStar />\n          </VuiIcon>\n        </VuiFlexItem>\n      ))}\n    </VuiFlexContainer>\n  );\n};\n";