export default "import { BiStar } from \"react-icons/bi\";\nimport {\n  BUTTON_COLOR,\n  VuiIconButton,\n  VuiFlexContainer,\n  VuiFlexItem,\n  VuiIcon,\n  BUTTON_SIZE,\n  VuiSpacer\n} from \"../../../lib\";\nimport { Subsection } from \"../../components/Subsection\";\n\nexport const IconButton = () => {\n  const icon = (\n    <VuiIcon>\n      <BiStar />\n    </VuiIcon>\n  );\n\n  return (\n    <>\n      <VuiFlexContainer>\n        {BUTTON_COLOR.map((color) => (\n          <VuiFlexItem grow={false} key={color}>\n            <VuiIconButton\n              aria-label=\"Example button\"\n              icon={icon}\n              color={color}\n              onClick={() => console.log(\"click\")}\n              onMouseOver={() => console.log(\"mouse over\")}\n              onMouseOut={() => console.log(\"mouse out\")}\n            />\n          </VuiFlexItem>\n        ))}\n      </VuiFlexContainer>\n\n      <VuiSpacer size=\"s\" />\n\n      <VuiFlexContainer>\n        {BUTTON_SIZE.map((size) => (\n          <VuiFlexItem grow={false} key={size}>\n            <VuiIconButton aria-label=\"Example button\" icon={icon} color=\"primary\" size={size} />\n          </VuiFlexItem>\n        ))}\n      </VuiFlexContainer>\n\n      <VuiSpacer size=\"s\" />\n\n      <Subsection title=\"With selected state\">\n        <VuiFlexContainer>\n          {BUTTON_COLOR.map((color) => (\n            <VuiFlexItem grow={false} key={color}>\n              <VuiIconButton\n                isSelected\n                aria-label=\"Button with selected state\"\n                icon={icon}\n                color={color}\n                onClick={() => console.log(\"click\")}\n                onMouseOver={() => console.log(\"mouse over\")}\n                onMouseOut={() => console.log(\"mouse out\")}\n              />\n            </VuiFlexItem>\n          ))}\n        </VuiFlexContainer>\n      </Subsection>\n    </>\n  );\n};\n";