export default "import { BiPlanet } from \"react-icons/bi\";\nimport {\n  VuiButtonPrimary,\n  VuiCard,\n  VuiFlexContainer,\n  VuiFlexItem,\n  VuiIcon,\n  VuiSpacer,\n  VuiText,\n  VuiTitle\n} from \"../../../lib\";\n\nexport const Card = () => {\n  return (\n    <VuiFlexContainer alignItems=\"stretch\">\n      <VuiFlexItem grow={1}>\n        <VuiCard\n          header={\n            <>\n              <VuiIcon color=\"neutral\" size=\"xl\">\n                <BiPlanet />\n              </VuiIcon>\n              <VuiSpacer size=\"s\" />\n              <VuiTitle size=\"s\">\n                <h4>Adventurer, the off-world colonies await you!</h4>\n              </VuiTitle>\n              <VuiSpacer size=\"s\" />\n              <VuiText>\n                <p>\n                  Alpha Centauri is the closest star system and closest planetary system to Earth's Solar System at 4.37\n                  light-years from the Sun.\n                </p>\n              </VuiText>\n            </>\n          }\n          body={<VuiButtonPrimary color=\"success\">Buy ticket</VuiButtonPrimary>}\n          interactive\n        />\n      </VuiFlexItem>\n\n      <VuiFlexItem grow={1}>\n        <VuiCard\n          align=\"center\"\n          header={\n            <>\n              <VuiIcon color=\"neutral\" size=\"xl\">\n                <BiPlanet />\n              </VuiIcon>\n              <VuiSpacer size=\"s\" />\n              <VuiTitle size=\"s\">\n                <h4>Adventurer, the off-world colonies await you!</h4>\n              </VuiTitle>\n              <VuiSpacer size=\"s\" />\n              <VuiText>\n                <p>\n                  Alpha Centauri is the closest star system and closest planetary system to Earth's Solar System at 4.37\n                  light-years from the Sun.\n                </p>\n              </VuiText>\n            </>\n          }\n          body={<VuiButtonPrimary color=\"success\">Buy ticket</VuiButtonPrimary>}\n          interactive\n        />\n      </VuiFlexItem>\n\n      <VuiFlexItem grow={1}>\n        <VuiCard\n          header={\n            <>\n              <VuiTitle size=\"s\">\n                <h4>Cards in cards</h4>\n              </VuiTitle>\n\n              <VuiSpacer size=\"s\" />\n\n              <VuiFlexContainer spacing=\"xxs\" wrap>\n                {[...Array(12)].map((el, index) => (\n                  <VuiFlexItem grow={false} key={index}>\n                    <VuiCard\n                      interactive\n                      header={\n                        <VuiIcon color=\"neutral\" size=\"xl\">\n                          <BiPlanet />\n                        </VuiIcon>\n                      }\n                    />\n                  </VuiFlexItem>\n                ))}\n              </VuiFlexContainer>\n            </>\n          }\n        />\n      </VuiFlexItem>\n    </VuiFlexContainer>\n  );\n};\n";