export default "import { Fragment } from \"react\";\nimport { SPACER_SIZE, VuiSpacer, VuiText } from \"../../../lib\";\n\nexport const Spacer = () => (\n  <>\n    {SPACER_SIZE.map((size) => (\n      <Fragment key={size}>\n        <VuiText>\n          <p>Size {size}</p>\n        </VuiText>\n\n        <div className=\"spacerExample\">\n          <VuiSpacer size={size} />\n        </div>\n\n        <VuiSpacer size=\"l\" />\n      </Fragment>\n    ))}\n  </>\n);\n";