export default "import { BADGE_COLOR, VuiBadge, VuiFlexContainer, VuiFlexItem } from \"../../../lib\";\n\nexport const BadgeColors = () => {\n  return (\n    <VuiFlexContainer>\n      {BADGE_COLOR.map((color) => (\n        <VuiFlexItem grow={false} key={color}>\n          <VuiBadge color={color}>Color {color}</VuiBadge>\n        </VuiFlexItem>\n      ))}\n    </VuiFlexContainer>\n  );\n};\n";