export default "import { VuiText, TEXT_SIZE, VuiSpacer } from \"../../../lib\";\n\nexport const Text = () => (\n  <>\n    {TEXT_SIZE.concat()\n      .reverse()\n      .map((size) => (\n        <>\n          <VuiText size={size} key={size}>\n            <p>\n              <strong>Demonstrating \"{size}\"-sized text.</strong>\n            </p>\n            <h1>Heading 1</h1>\n            <h2>Heading 2</h2>\n            <h3>Heading 3</h3>\n            <h4>Heading 4</h4>\n            <h5>Heading 5</h5>\n            <h6>Heading 6</h6>\n            <p>\n              Paragraph with some <strong>bold</strong> and <em>emphasized</em> words in it. Let's make this really,\n              really, really, super-duper, ridiculously long just so we can be sure the text wraps and you can get idea\n              of the line-height.\n            </p>\n\n            <p>\n              Here's another paragraph with a <a href=\"#\">link</a> in it.\n            </p>\n\n            <ul>\n              <li>One potato</li>\n              <li>Two potato</li>\n              <li>Three potato</li>\n              <li>Four</li>\n            </ul>\n\n            <ol>\n              <li>Five potato</li>\n              <li>Six potato</li>\n              <li>Seven potato</li>\n              <li>More</li>\n            </ol>\n\n            <p>\n              <code>doc.title='Vectara'</code>\n            </p>\n          </VuiText>\n\n          <VuiSpacer size=\"m\" />\n        </>\n      ))}\n  </>\n);\n";