export default "import { useState } from \"react\";\nimport { VuiOptionsList } from \"../../../lib\";\n\nconst options = [\n  { value: \"a\", label: \"A\" },\n  { value: \"b\", label: \"B\" },\n  { value: \"c\", label: \"C\" },\n  { value: \"d\", label: \"D\" },\n  { value: \"e\", label: \"E\" },\n  { value: \"f\", label: \"F\" },\n  { value: \"g\", label: \"G\" },\n  { value: \"h\", label: \"H\" },\n  { value: \"i\", label: \"I\" },\n  { value: \"j\", label: \"J\" },\n  { value: \"k\", label: \"K\" },\n  { value: \"l\", label: \"L\" }\n];\n\nexport const ScrollableOptionsList = () => {\n  const [selectedOption, setSelectedOption] = useState(\"a\");\n\n  return (\n    <VuiOptionsList\n      isSelectable\n      isScrollable\n      onSelectOption={(value: string) => {\n        setSelectedOption(value);\n      }}\n      selected={selectedOption}\n      options={options}\n    />\n  );\n};\n";