export default "import { VuiCard, VuiText, VuiTitle } from \"../../../lib\";\n\nexport const Highlight = () => {\n  return (\n    <VuiCard\n      header={\n        <VuiTitle size=\"m\">\n          <h4>Replicant</h4>\n        </VuiTitle>\n      }\n      body={\n        <VuiText>\n          <p>A genetically engineered or artificial being created as an exact replica of a particular human being.</p>\n        </VuiText>\n      }\n      highlight\n      ungrouped\n    />\n  );\n};\n";