export default "import { VuiSpacer, VuiStatus } from \"../../../lib\";\n\nexport const Status = () => {\n  return (\n    <>\n      <VuiStatus status=\"error\" label=\"Error\" />\n\n      <VuiSpacer size=\"m\" />\n\n      <VuiStatus status=\"warning\" label=\"Warning\" />\n\n      <VuiSpacer size=\"m\" />\n\n      <VuiStatus status=\"success\" label=\"Success\" />\n\n      <VuiSpacer size=\"m\" />\n\n      <VuiStatus status=\"info\" label=\"Info\" />\n\n      <VuiSpacer size=\"m\" />\n    </>\n  );\n};\n";