export default "import { useState } from \"react\";\nimport { VuiButtonPrimary, VuiFlexContainer, VuiFlexItem, VuiSearchInput } from \"../../../lib\";\n\nexport const Large = () => {\n  const [searchValue, setSearchValue] = useState(\"\");\n\n  return (\n    <VuiFlexContainer alignItems=\"center\">\n      <VuiFlexItem grow={1}>\n        <VuiSearchInput\n          size=\"l\"\n          placeholder=\"Ask a question\"\n          value={searchValue}\n          onChange={(e) => setSearchValue(e.target.value)}\n          onSubmit={(e) => {\n            e.preventDefault();\n            alert(`Submit ${searchValue}`);\n          }}\n        />\n      </VuiFlexItem>\n\n      <VuiFlexItem>\n        <VuiButtonPrimary color=\"primary\" size=\"l\">\n          Search\n        </VuiButtonPrimary>\n      </VuiFlexItem>\n    </VuiFlexContainer>\n  );\n};\n";