export default "import { useState } from \"react\";\nimport { VuiInfoMenu, VuiButtonSecondary, VuiIcon, VuiOptionsList, VuiSpacer, VuiText } from \"../../../lib\";\nimport { BiSolidUser } from \"react-icons/bi\";\n\nconst options = [\n  { value: \"edit\", label: \"Update profile\" },\n  { value: \"signOut\", label: \"Sign out\" }\n];\n\nexport const InfoMenu = () => {\n  const [isOpen, setIsOpen] = useState(false);\n\n  return (\n    <VuiInfoMenu\n      data-testid=\"infoMenu\"\n      anchorSide=\"left\"\n      isOpen={isOpen}\n      setIsOpen={setIsOpen}\n      button={\n        <VuiButtonSecondary\n          color=\"accent\"\n          size=\"m\"\n          icon={\n            <VuiIcon size=\"m\">\n              <BiSolidUser />\n            </VuiIcon>\n          }\n        >\n          email@email.com\n        </VuiButtonSecondary>\n      }\n      info={[\n        { title: \"Email\", value: \"email@email.com\" },\n        { title: \"Account number\", value: \"1234567890\" },\n        {\n          title: \"Account size\",\n          value: (\n            <>\n              <VuiText>\n                <p>22 MB</p>\n              </VuiText>\n\n              <VuiSpacer size=\"xs\" />\n\n              <VuiButtonSecondary size=\"xs\" color=\"neutral\">\n                Refresh\n              </VuiButtonSecondary>\n            </>\n          )\n        }\n      ]}\n    >\n      <VuiOptionsList\n        onSelectOption={(value: string) => {\n          alert(`Selected ${value}`);\n        }}\n        options={options}\n        size=\"l\"\n      />\n    </VuiInfoMenu>\n  );\n};\n";