export default "import { BiCheck } from \"react-icons/bi\";\nimport { VuiFlexContainer, VuiList, VuiFlexItem, VuiSpinner, VuiIcon, VuiText, VuiTextColor } from \"../../../lib\";\n\nexport const List = () => {\n  const items = [\n    {\n      key: \"selectStrategy\",\n      isComplete: true,\n      render: () => (\n        <VuiFlexContainer alignItems=\"center\" spacing=\"s\">\n          <VuiFlexItem>\n            <VuiIcon color=\"success\" size=\"s\">\n              <BiCheck />\n            </VuiIcon>\n          </VuiFlexItem>\n\n          <VuiFlexItem grow={false}>\n            <VuiText>\n              <p>Selected strategy</p>\n            </VuiText>\n          </VuiFlexItem>\n        </VuiFlexContainer>\n      )\n    },\n    {\n      key: \"retrieveInfo\",\n      isComplete: true,\n      render: () => (\n        <VuiFlexContainer alignItems=\"center\" spacing=\"m\">\n          <VuiFlexItem>\n            <VuiSpinner size=\"xs\" />\n          </VuiFlexItem>\n\n          <VuiFlexItem grow={false}>\n            <VuiText>\n              <p>Retrieving information</p>\n            </VuiText>\n          </VuiFlexItem>\n        </VuiFlexContainer>\n      )\n    },\n    {\n      key: \"generateSummary\",\n      isComplete: false,\n      render: () => (\n        <VuiText>\n          <p>\n            <VuiTextColor color=\"subdued\">Generate summary</VuiTextColor>\n          </p>\n        </VuiText>\n      )\n    }\n  ];\n\n  return <VuiList items={items} />;\n};\n";