export default "import { SPINNER_SIZE, VuiSpinner } from \"../../../lib\";\nimport { Subsection } from \"../../components/Subsection\";\n\nexport const Sizes = () => {\n  return (\n    <>\n      {SPINNER_SIZE.map((size) => (\n        <Subsection title={`Size ${size}`} key={size}>\n          <VuiSpinner size={size} />\n        </Subsection>\n      ))}\n    </>\n  );\n};\n";