export default "import { useState } from \"react\";\nimport { BiStar } from \"react-icons/bi\";\nimport {\n  BUTTON_SIZE,\n  VuiButtonPrimary,\n  VuiButtonSecondary,\n  VuiButtonTertiary,\n  VuiFlexContainer,\n  VuiFlexItem,\n  VuiIcon,\n  VuiSpacer,\n  VuiToggle\n} from \"../../../lib\";\nimport { Subsection } from \"../../components/Subsection\";\n\nexport const Sizes = () => {\n  const [isLoading, setIsLoading] = useState<boolean>(false);\n\n  const icon = (\n    <VuiIcon>\n      <BiStar />\n    </VuiIcon>\n  );\n\n  return (\n    <>\n      <VuiToggle label=\"Loading\" checked={isLoading} onChange={() => setIsLoading(!isLoading)} />\n\n      <VuiSpacer size=\"m\" />\n\n      <Subsection title=\"Primary button\">\n        <VuiFlexContainer>\n          {BUTTON_SIZE.map((size) => (\n            <VuiFlexItem grow={false} key={size}>\n              <VuiButtonPrimary\n                icon={icon}\n                color=\"primary\"\n                size={size}\n                isLoading={isLoading}\n                onClick={() => console.log(\"click\")}\n                onMouseOver={() => console.log(\"mouse over\")}\n                onMouseOut={() => console.log(\"mouse out\")}\n              >\n                Size {size}\n              </VuiButtonPrimary>\n            </VuiFlexItem>\n          ))}\n        </VuiFlexContainer>\n      </Subsection>\n\n      <Subsection title=\"Secondary button\">\n        <VuiFlexContainer>\n          {BUTTON_SIZE.map((size) => (\n            <VuiFlexItem grow={false} key={size}>\n              <VuiButtonSecondary icon={icon} color=\"primary\" size={size} isLoading={isLoading}>\n                Size {size}\n              </VuiButtonSecondary>\n            </VuiFlexItem>\n          ))}\n        </VuiFlexContainer>\n      </Subsection>\n\n      <Subsection title=\"Tertiary button\">\n        <VuiFlexContainer>\n          {BUTTON_SIZE.map((size) => (\n            <VuiFlexItem grow={false} key={size}>\n              <VuiButtonTertiary icon={icon} color=\"primary\" size={size} isLoading={isLoading}>\n                Size {size}\n              </VuiButtonTertiary>\n            </VuiFlexItem>\n          ))}\n        </VuiFlexContainer>\n      </Subsection>\n    </>\n  );\n};\n";